import { Modal, message } from 'ant-design-vue';
import BfdUtil from '@baifendian/adherev-util';
import Intl from '@baifendian/adherev-util-intl';
import { openSuccessDialog, openSuccessMessage } from './successprompt';
var withVue = BfdUtil._util.withVue;
var SuccessPrompt = {
    openSuccessDialog: openSuccessDialog,
    openSuccessMessage: openSuccessMessage,
    isUse: function () { return true; },
    use: function (Vue) {
        Intl.isUse() && Intl.use(Vue);
        Vue.use(message);
        Vue.use(Modal);
        withVue(Vue, 'SuccessPrompt', SuccessPrompt);
    },
};
export default SuccessPrompt;
